// https://github.com/twbs/bootstrap-sass/blob/master/assets/stylesheets/bootstrap/_variables.scss

// Glyphicons font path
$icon-font-path:        "../fonts/";

$screen-xxxs-min: 0;
$screen-xxs-min: 320px;
$screen-xs-min: 568px;
$screen-sm-min: 667px;
$screen-md-min: 768px;
$screen-lg-min: 992px;
$screen-xl-min: 1200px;
$screen-xxl-min: 1440px;
$screen-xxxl-min: 1600px;

$screen-xxxs-max: 0;
$screen-xxs-max: 319px;
$screen-xs-max: 567px;
$screen-sm-max: 666px;
$screen-md-max: 767px;
$screen-lg-max: 991px;
$screen-xl-max: 1199px;
$screen-xxl-max: 1439px;
$screen-xxxl-max: 1599px;

//== Grid system
//
//## Define your custom responsive grid.

//** Number of columns in the grid.
$grid-columns:              12;
//** Padding between columns. Gets divided in half for the left and right.
$grid-gutter-width:         30px;
// Navbar collapse
//** Point at which the navbar becomes uncollapsed.
$grid-float-breakpoint:     $screen-sm-min;
//** Point at which the navbar begins collapsing.
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1);

// Grid settings
$main-sm-columns:       12;
$sidebar-sm-columns:    4;

//== Colors
$white       : #ffffff;
$black       : #0C1B2E;
$gray-100    : #f9f9f9;
$gray-150    : #f1f2f7;
$gray-200    : #efeff0;
$gray-300    : #A0A0A0;
$gray-400    : #656565;
$gray-500    : #363636;
$gray-700    : #1E1E1E;
$blue-100    : #c8e7ff;
$blue-200    : #6cabdc;
$blue-400    : #00457a;
$blue-500    : #003f70;
$blue-700    : #0b1b2e;
$blue-800    : #112338;
$orange-400  : #fe782e;

$colors: (
	'white'      : $white,
	'black'      : $black,
	'gray-100'   : $gray-100,
	'gray-150'   : $gray-150,
	'gray-200'   : $gray-200,
	'blue-100'   : $blue-100,
	'blue-200'   : $blue-200,
	'blue-400'   : $blue-400,
	'blue-500'   : $blue-500,
	'blue-700'   : $blue-700,
	'orange-400' : $orange-400
);

$colors-names: (
	'white',
	'black',
	'gray-100',
	'gray-150',
	'gray-200',
	'blue-100',
	'blue-200',
	'blue-400',
	'blue-500',
	'blue-700',
	'orange-400'
);

$colors-hover: (
	'white'      : $white,
	'black'      : darken($black,10%),
	'gray-100'   : darken($gray-100,10%),
	'gray-150'   : darken($gray-150,10%),
	'gray-200'   : darken($gray-200,10%),
	'blue-100'   : darken($blue-100,10%),
	'blue-200'   : darken($blue-200,10%),
	'blue-400'   : darken($blue-400,10%),
	'blue-500'   : darken($blue-500,10%),
	'blue-700'   : darken($blue-700,10%),
	'orange-400' : darken($orange-400,10%)
);

$gradients : (
	'blue-700-to-blue-400' : linear-gradient(135deg, rgba($blue-700,1) 0%, rgba($blue-400,1) 100%)
);
//== Scaffolding
//
//## Settings for some of the most global styles.

//** Background color for `<body>`.
$body-bg:               #ffffff;
//** Global text color on `<body>`.
$text-color:             $gray-700;

//** Global textual link color.
$link-color:            $blue-400;
$link-decoration: underline;
//** Link hover color set via `darken()` function.
$link-hover-color:      $blue-400;
//** Link hover decoration.
$link-hover-decoration: underline;

//== Typography
//
//## Font, line-height, and color for body text, headings, and more.
$font-family-headings     : "Source Sans Pro","Helvetica Neue", Helvetica, Arial, sans-serif;
$font-family-sans-serif   : "Source Sans Pro","Helvetica Neue", Helvetica, Arial, sans-serif;
$font-family-serif        : Georgia, "Times New Roman", Times, serif;
$font-family-monospace    : Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-base         : $font-family-sans-serif;
$font-size-base           : calc(16px * 1.3333);
$font-size-large          : ceil(($font-size-base * 1.25)); // ~18px
$font-size-small          : ceil(($font-size-base * 0.85)); // ~12px
$font-size-h1             : calc(46px * 1.3333); // ~36px
$font-size-h1-line-height : calc(50px * 1.3333);
$font-size-h2             : calc(40px * 1.3333); // ~36px
$font-size-h2-line-height : calc(44px * 1.3333);
$font-size-h3             : calc(36px * 1.3333); // ~36px
$font-size-h3-line-height : calc(40px * 1.3333);
$font-size-h4             : calc(26px * 1.3333); // ~36px
$font-size-h4-line-height : calc(28px * 1.3333);
$font-size-h5             : calc(22px * 1.3333); // ~36px
$font-size-h5-line-height : calc(32px * 1.3333);
$font-size-h6             : calc(14px * 1.3333); // ~36px
$font-size-h6-line-height : calc(19px * 1.3333);

//** Unit-less `line-height` for use in components like buttons.
$line-height-base:        1.428571429; // 20/14
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed:    floor(($font-size-base * $line-height-base)); // ~20px

//** By default, this inherits from the `<body>`.
$headings-font-family:    inherit;
$headings-font-weight:    300;
$headings-line-height:    1.2;
$headings-color:          inherit;